<template>
  <div class="wrapper" ref="cityList">
    <div class="content">
          <div class="area">
      <div class="area-header">
        <span class="area-title">当前城市</span>
      </div>
      <div class="area-body city-now">
          <button class="area-body-btn">北京</button>
      </div>
    </div>
    <div class="area">
      <div class="area-header">
        <span class="area-title">热门城市</span>
      </div>
      <div class="area-body city-hot">
          <button class="area-body-btn" v-for="hotCity of hotCities" :key="hotCity.id">{{hotCity.name}}</button>
      </div>
    </div>
    <div class="area" v-for="(cityItems,key,index) of cities" :key="index" :ref="key">
      <div class="area-header">
        <span class="area-title">{{key}}</span>
      </div>
      <div class="area-body city-list">
          <div class="city-item" v-for="city of cityItems" :key="city.id">{{city.name}}</div>
      </div>
    </div>
    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  name: 'CityList',
  props: {
    cities: Object,
    hotCities: Array,
    letter: String
  },
  data: function () {
    return {
    }
  },
  watch: {
    // 监听letter的变化，变化就滚动到当前字母下的城市列表
    letter () {
      if (this.letter) {
        // console.log(this.letter)
        let ele = this.$refs[this.letter][0]
        // console.log(ele)
        this.scroll.scrollToElement(ele)
      }
    }
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.cityList)
    console.log(new BScroll(this.$refs.cityList))
  }
}
</script>
<style lang="stylus" scoped>
.wrapper
  position absolute
  top 160px
  right 0
  left 0
  bottom 0
  overflow hidden
  .area
    .area-header
      height 62px
      line-height 62px
      font-size 28px
      background #ddd
      border-bottom #ccc
      .area-title
        margin-left 10px
    .area-body
      padding-right 30px
      .area-body-btn
        border 1px solid #ddd
        padding 5px 60px
        border-radius 6px
        background #fff
        margin 20px 40px 20px 10px
        font-size 28px
      .city-item
        border-bottom 1px solid #ddd
        padding-left 10px
        line-height 86px
        height 86px
</style>



