<template>
    <div class="recommend">
      <h3 class="recommend-header-title">推荐景点</h3>
      <div class="recommend-item" v-for="item of recommendList" :key="item.id">
          <div class="recommend-left">
            <img :src="item.imgUrl" />
          </div>
          <div class="recommend-right">
            <h5 class="recommend-right-title">{{item.title}}</h5>
            <p class="recommend-right-desc">{{item.desc}}</p>
            <button class="recommend-right-btn">查看详情</button>
          </div>
      </div>
    </div>
</template>
<script>
export default {
  name: 'HomeRecommend',
  props: {
    recommendList: Array
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
.recommend
  margin-top 15px
  .recommend-header-title
    margin-left 20px
    color $darkTextColor
    font-weight normal
    height 44px
    line-height 44px
    padding 15px
    border-bottom 1px solid #ddd
  .recommend-item
    display flex
    overflow hidden
    padding-top 30px
    padding-left 30px
    padding-right 30px
    height 0
    padding-bottom 22%
    border-bottom 1px solid #ddd
    .recommend-left
      float left
      width 140px
      height 140px
      img
        width 140px
    .recommend-right
      flex 1
      min-width 0
      margin-left 30px
      margin-right 30px
      .recommend-right-title
        font-size 32px
        font-weight normal
        ellipsis()
      .recommend-right-desc
        margin-top 10px
        ellipsis()
        color #8e8e8e
      .recommend-right-btn
        background #ff9300
        color #fff
        border 1px solid #ddd
        margin-top 20px
        border-radius 6px
</style>



