<template>
    <div class="wrapper">
    <home-header :city="city"></home-header>
    <home-swiper :swiperList="indexData.swiperList"></home-swiper>
    <home-icons :list="indexData.iconList"></home-icons>
    <home-recommend :recommendList="indexData.recommendList"></home-recommend>
    <home-weekend :weekendList="indexData.weekendList"></home-weekend>
    </div>
</template>
<script>
import HomeHeader from './components/Header'
import HomeSwiper from './components/Swiper'
import HomeIcons from './components/Icons'
import HomeRecommend from './components/Recommend'
import HomeWeekend from './components/Weekend'
import axios from 'axios'
export default {
  name: 'Home',
  data: function () {
    const indexData = {}
    return {
      city: '北京',
      indexData
    }
  },
  components: {
    HomeHeader,
    HomeSwiper,
    HomeIcons,
    HomeRecommend,
    HomeWeekend
  },
  methods: {
    getIndexData: function () {
      axios.get('./static/mock/index.json').then((res) => {
        this.$data.indexData = res.data.data
      })
    }
  },
  mounted: function () {
    this.getIndexData()
  }
}
</script>
<style scoped>
</style>



