<template>
  <div class="weekend">
    <h3 class="weekend-title">周末去哪儿</h3>
    <div class="weekend-item" v-for="item of weekendList" :key="item.id">
      <div class="weekend-img">
        <img :src="item.imgUrl" />
      </div>
      <div class="weekend-info">
            <h5>{{item.title}}</h5>
            <p>{{item.desc}}</p>
      </div>
    </div>

  </div>
</template>
<script>
export default {
  name: 'HomeWeekend',
  props: {
    weekendList: Array
  }
}
</script>
<style lang="stylus" scoped>
@import '~styles/varibles.styl'
@import '~styles/mixins.styl'
.weekend
  .weekend-title
    margin-left 20px
    color $darkTextColor
    font-weight normal
    height 44px
    line-height 44px
    padding 15px
    border-bottom 1px solid #ddd
  .weekend-item
    padding-bottom 15px
    border-bottom 1px solid #ddd
    .weekend-img
      overflow hidden
      height 0
      padding-bottom 33.9%
      background red
      img
        width 100%
    .weekend-info
      margin-left 20px
      margin-right 20px
      h5
        margin-top 30px
        font-size 32px
        font-weight normal
        ellipsis()
      p
        margin-top 10px
        ellipsis()
        color #8e8e8e
</style>



