<template>
  <div class="icons">
    <swiper :options="swiperOption">
      <swiper-slide v-for="(page,index) of pages" :key="index">
        <div class="icon" v-for="item of page" :key="item.id">
          <div class="icon-img">
            <img :src="item.imgUrl" :alt="item.desc">
          </div>
          <p class="icon-desc">{{item.desc}}</p>
        </div>
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'icons',
  props: {
    list: {
      type: Array,
      default: function () {
        return []
      }}
  },
  data: function () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        }
      }
    }
  },
  computed: {
    pages: function () {
      const pages = []
      this.list.forEach((icon, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(icon)
      })
      return pages
    }
  },
  components: {
    swiper,
    swiperSlide
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl';
@import '~styles/mixins.styl';
      .icons >>> .swiper-pagination-bullet-active
        background $darkTextColor
.icons {
  width: 100%;
  height: 0;
  overflow: hidden;
  padding-bottom: 50%;

  .icon {
    float: left;
    position: relative;
    width: 25%;
    height: 0;
    overflow: hidden;
    padding-bottom: 25%;

    .icon-img {
      position: absolute;
      padding: 15px;
      right: 0;
      left: 0;
      top: 0;
      bottom: 44px;

      img {
        display: block;
        height: 100%;
        margin: 0 auto;
      }
    }

    .icon-desc {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 44px;
      padding: 0;
      margin: 0;
      line-height: 44px;
      text-align: center;
      color: $darkTextColor;
      ellipsis()
    }
  }
}
</style>



