<template>
      <ul class="alphabet">
        <li class="alphabet-item" v-for="letter of letters" :key="letter" :ref="letter"
        @touchstart="handleTouchStart"
        @touchmove="handleTouchMove"
        @touchend="handleTouchEnd"
        @click="handleAplphabet"
        >{{letter}}
        </li>
      </ul>
</template>
<script>
export default {
  name: 'CityAlphabet',
  props: {
    cities: Object
  },
  data: function () {
    return {
      touchStatus: false,
      startY: 0
    }
  },
  // 当页面重新渲染后，或者数据有变动的时候
  updated () {
    this.startY = this.$refs['A'][0].offsetTop
  },
  computed: {
    letters () {
      let letters = []
      for (let i in this.cities) {
        letters.push(i)
      }
      return letters
    }
  },
  methods: {
    handleAplphabet (e) {
      this.$emit('change', e.target.innerText)
      // console.log(e.target.innerHTML)
    },
    handleTouchStart () {
      this.touchStatus = true
    },
    handleTouchMove (e) {
      if (this.touchStatus) {
        // 获取物理尺寸下的像素数
        let touchY = e.touches[0].clientY - 140
        let index = Math.floor((touchY - this.startY) / 20)
        this.$emit('change', this.letters[index])
      }
    },
    handleTouchEnd () {
      this.touchStatus = false
      console.log('触摸结束')
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~styles/varibles.styl'
.alphabet
  position absolute
  right 0
  top 280px
  width 40px
  .alphabet-item
    height 40px
    line-height 40px
    text-align center
    color $bgColor
</style>



