<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont icon-search">&#xe632;</span>
      输入城市/景点/游玩主题
    </div>

    <div class="header-right">
          <router-link to="/city">{{city}}</router-link>
          <span class="iconfont icon-arrow">&#xe6aa;</span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'Header',
  props: {
    city: String
  }
}
</script>
<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header
    font-size 26px
    display flex
    line-height $headerHeight
    height $headerHeight
    background $bgColor
    color #fff
    .header-left
      float left
      margin-left 10px
      div
        font-size 40px
    .header-input
      margin-left 20px
      flex 1
      background #fff
      margin-top 11px
      border-radius 5px
      height 64px
      line-height 64px
      color #ccc
      .icon-search
        margin-left 10px
        font-size 30px
    .header-right
      float right
      margin-left 20px
      a
        color #fff
        text-decoration none
      .icon-arrow
        font-size 30px
        margin-right 10px
</style>



